import { useTranslation } from "react-i18next";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import { useCallback, useState } from "react";
import style from "../../FinanceManager/FinanceManager.module.css";
import CreateButton from "../../../Ui/Button/CreateButton";
import AddOutward from "./AddOutward";
import OutwardApproved from "./OutwardApproved";
import Outward from "./Outward";
import { useInoutwardOutwardList } from "../../../Hooks/ApiHooks/InOutwardManager/inoutward-manager-api";
const OutwardConfig = ({ ioid, staffId, instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const [step, setStep] = useState("");
  const onNavigate = useCallback(() => {
    let url = "";
    url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/member?a=ioward`
        : `/q/${params.username}/member?a=ioward`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);

  const { inoutwardOutwardListRefetch } = useInoutwardOutwardList({
    data: {
      ioid: ioid,
      page: 1,
      limit: 10,
      search: "",
    },
    skip: !ioid,
  });

  const onRefetch = useCallback(() => {
    inoutwardOutwardListRefetch();
  }, [inoutwardOutwardListRefetch]);

  const onCloseStep = () => {
    setStep("");
  };

  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <div
            className={style.fm_header_start_container}
            style={{
              gap: "0.2rem",
            }}
          >
            <img
              alt="search icon"
              src={`${assestsUtilityUrl}/back.svg`}
              style={{
                height: "2rem",
                width: "2rem",
              }}
              onClick={onNavigate}
            />
            <h6>{t("outward")}</h6>
          </div>
          <CreateButton
            label="add"
            customStyle={{
              margin: "0",
              padding: "0.4rem 1rem",
              width: "fit-content",
            }}
            onAction={() => setStep(1)}
          />
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />

      <HorizontalTabWrapper>
        {tabList.map((tab) => (
          <HorizontalTabCard
            key={tab?.id}
            tab={tab}
            isLink
            isActive={tab.groupIs?.includes(searchParams.get("n"))}
            parentState={getQuery.state}
            url={
              getQuery.state?.urlOffSet === "Institute"
                ? `/q/${params.username}/member?a=ioward&type=outward${tab.urlKey}`
                : `/q/${params.username}/member?a=ioward&type=outward${tab.urlKey}`
            }
          />
        ))}
      </HorizontalTabWrapper>
      <BorderBottom
        customStyleBorder={{
          marginTop: "0",
        }}
      />

      {searchParams.get("n") === "approved" ? (
        <OutwardApproved ioid={ioid} />
      ) : (
        <Outward ioid={ioid} />
      )}

      {step === 1 ? (
        <AddOutward
          instituteId={instituteId}
          staffId={staffId}
          onClose={onCloseStep}
          onRefetch={onRefetch}
          ioid={ioid}
        />
      ) : null}
    </>
  );
};

export default OutwardConfig;
const tabList = [
  {
    label: "pending",
    id: "pending",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "approved",
    id: "approved",
    urlKey: "&n=approved",
    groupIs: ["approved"],
  },
];
