import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useEffect, useState } from "react";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import InwardApprovedCard from "./InwardApprovedCard";
import { useInoutwardInwardApproveList } from "../../../Hooks/ApiHooks/InOutwardManager/inoutward-manager-api";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/FinanceManager.module.css";
const InwardApproved = ({ ioid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const { inoutwardInwardApproveList, inoutwardInwardApproveListLoading } =
    useInoutwardInwardApproveList({
      data: {
        ioid: ioid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !ioid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (inoutwardInwardApproveList?.list?.length) {
      let obj = nextPage(inoutwardInwardApproveList?.list?.length);
      setState(() => obj);
    }
  }, [inoutwardInwardApproveList?.list?.length]);

  const onSearchEvent = (e) => {
    if (
      !inoutwardInwardApproveListLoading &&
      inoutwardInwardApproveList?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };

  return (
    <>
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
                onKeyUp={onCallBindSearch}
                value={searchCopy}
              />
            </div>
          </section>
        </div>
        {inoutwardInwardApproveList?.list?.map((outward, index) =>
          inoutwardInwardApproveList?.list?.length === index + 1 ? (
            <div key={outward?._id} ref={ref}>
              <InwardApprovedCard outward={outward} />
            </div>
          ) : (
            <InwardApprovedCard outward={outward} key={outward?._id} />
          )
        )}
        {inoutwardInwardApproveListLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
          )
        ) : search ? (
          inoutwardInwardApproveList?.list?.length === 0 && (
            <EmptyMessage
              title="No item found related this search."
              customStyleContainer={{
                marginTop: "2rem",
              }}
            />
          )
        ) : (
          inoutwardInwardApproveList?.list?.length === 0 && (
            <EmptyMessage
              title="No item."
              customStyleContainer={{
                marginTop: "2rem",
              }}
            />
          )
        )}
      </UserMemberContentWrapper>
    </>
  );
};

export default InwardApproved;
