import { useTranslation } from "react-i18next";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import HorizontalTabCard from "../../../../Ui/Tab/Horizontal/HorizontalTabCard";
import HorizontalTabWrapper from "../../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import InOutwardSentRequestPending from "./InOutwardSentRequestPending";
import InOutwardSentRequestApproved from "./InOutwardSentRequestApproved";
import { assestsUtilityUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useCallback } from "react";
import style from "../../../FinanceManager/FinanceManager.module.css";

const InOutwardSentRequestConfig = ({ staffId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();

  const onNavigate = useCallback(() => {
    let url = "";
    url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/member?a=ioward`
        : `/q/${params.username}/member?a=ioward`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);

  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <div
            className={style.fm_header_start_container}
            style={{
              gap: "0.2rem",
            }}
          >
            <img
              alt="search icon"
              src={`${assestsUtilityUrl}/back.svg`}
              style={{
                height: "2rem",
                width: "2rem",
              }}
              onClick={onNavigate}
            />
            <h6>{t("sent_inout_request")}</h6>
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />

      <HorizontalTabWrapper>
        {tabList.map((tab) => (
          <HorizontalTabCard
            key={tab?.id}
            tab={tab}
            isLink
            isActive={tab.groupIs?.includes(searchParams.get("n"))}
            parentState={getQuery.state}
            url={
              getQuery.state?.urlOffSet === "Institute"
                ? `/q/${params.username}/member?a=ioward&type=sent${tab.urlKey}`
                : `/q/${params.username}/member?a=ioward&type=sent${tab.urlKey}`
            }
          />
        ))}
      </HorizontalTabWrapper>
      <BorderBottom
        customStyleBorder={{
          marginTop: "0",
        }}
      />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0.2rem",
        }}
      >
        {searchParams.get("n") === "approved" ? (
          <InOutwardSentRequestApproved staffId={staffId} />
        ) : (
          <InOutwardSentRequestPending staffId={staffId} />
        )}
      </UserMemberContentWrapper>
    </>
  );
};

export default InOutwardSentRequestConfig;
const tabList = [
  {
    label: "pending",
    id: "pending",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "approved",
    id: "approved",
    urlKey: "&n=approved",
    groupIs: ["approved"],
  },
];
