import { useTranslation } from "react-i18next";
import style from "../../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import { Link, useLocation, useParams } from "react-router-dom";
import dayjs from "dayjs";
import { localTimeFormat } from "../../../../Utils/Functions/localTime";
import { assestsFeedUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";

const InOutwardSentRequestPendingCard = ({ outward }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();

  return (
    <>
      <Link
        className={style.sf_card}
        to={`/q/${params.username}/member?a=ioward&type=ward-detail`}
        state={{
          ...getQuery.state,
          outward,
          isLastShow: outward?.model_type === "INWARD" ? "Inward" : "Outward",
          menuControll: "Yes",
        }}
      >
        <div
          className={style.iowc_container}
          style={{
            width: "100%",
          }}
        >
          <div className={style.iowc_container}>
            <img
              src={`${assestsFeedUrl}/announcement.svg`}
              alt="announcement"
            />
            <div className={style.iowc_container_inner}>
              {outward?.model_type === "INWARD" ? (
                <>
                  <h6>
                    <span
                      style={{
                        fontWeight: "500",
                      }}
                    >
                      {t("name")}
                      {": "}
                    </span>
                    {outward?.name ?? ""}
                  </h6>
                  <h6>
                    <span
                      style={{
                        fontWeight: "500",
                      }}
                    >
                      {t("no")}
                      {": "}
                    </span>
                    {outward?.number ?? ""}
                  </h6>
                </>
              ) : (
                <>
                  <h6>
                    <span
                      style={{
                        fontWeight: "500",
                      }}
                    >
                      {t("outward_no")}
                      {": "}
                    </span>

                    {outward?.outward_number ?? ""}
                  </h6>
                  <h6>
                    <span
                      style={{
                        fontWeight: "500",
                      }}
                    >
                      {t("subject_only")}
                      {": "}
                    </span>
                    {outward?.subject ?? ""}
                  </h6>
                </>
              )}
            </div>
          </div>
          <div className={style.iowc_container_inner}>
            <h6>
              <span
                style={{
                  fontWeight: "500",
                }}
              >
                {t("type")}
                {": "}
              </span>

              {outward?.outward_type ?? ""}
            </h6>
            <h6>
              <span
                style={{
                  fontWeight: "500",
                }}
              >
                {t("created_at")}
                {": "}
              </span>
              {dayjs(outward?.created_at)?.format("DD/MM/YYYY")},
              {localTimeFormat(outward?.created_at, "LT")}
            </h6>
          </div>
        </div>
      </Link>
    </>
  );
};

export default InOutwardSentRequestPendingCard;
