import { useTranslation } from "react-i18next";
import style from "../../../../InstituteMember/InstituteFeedback/InstituteFeedback.module.css";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { Link, useLocation, useParams } from "react-router-dom";
import dayjs from "dayjs";
import { localTimeFormat } from "../../../../Utils/Functions/localTime";
import { pdfCdnUrl } from "../../../../Services/UrlConfig/BaseUrl";

const InOutwardReceiveRequestApprovedCard = ({ outward }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const onPdfOpen = (e) => {
    e.stopPropagation();
  };
  return (
    <>
      <Link
        className={style.sf_card}
        to={`/q/${params.username}/member?a=ioward&type=ward-detail`}
        state={{
          ...getQuery.state,
          outward,
          isLastShow: outward?.model_type === "INWARD" ? "Inward" : "Outward",
        }}
      >
        <div
          className={style.iowc_container}
          style={{
            width: "100%",
          }}
        >
          <div className={style.iowc_container}>
            <img
              src={`${assestsFeedUrl}/announcement.svg`}
              alt="announcement"
            />
            <div className={style.iowc_container_inner}>
              {outward?.model_type === "INWARD" ? (
                <>
                  <h6>
                    <span
                      style={{
                        fontWeight: "500",
                      }}
                    >
                      {t("name")}
                      {": "}
                    </span>
                    {outward?.name ?? ""}
                  </h6>
                  <h6>
                    <span
                      style={{
                        fontWeight: "500",
                      }}
                    >
                      {t("no")}
                      {": "}
                    </span>
                    {outward?.number ?? ""}
                  </h6>
                </>
              ) : (
                <>
                  <h6>
                    <span
                      style={{
                        fontWeight: "500",
                      }}
                    >
                      {t("outward_no")}
                      {": "}
                    </span>

                    {outward?.outward_number ?? ""}
                  </h6>
                  <h6>
                    <span
                      style={{
                        fontWeight: "500",
                      }}
                    >
                      {t("subject_only")}
                      {": "}
                    </span>
                    {outward?.subject ?? ""}
                  </h6>
                </>
              )}

              <h6>
                <span
                  style={{
                    fontWeight: "500",
                  }}
                >
                  {t("prepared_by")}
                  {": "}
                </span>
                {`${outward?.prepare_by?.staffFirstName ?? ""} ${
                  outward?.prepare_by?.staffMiddleName ?? ""
                } ${outward?.prepare_by?.staffLastName ?? ""}`}
              </h6>
            </div>
          </div>
          <div className={style.iowc_container_inner}>
            {outward?.generated_report ? (
              <a
                href={`${pdfCdnUrl}/${outward?.generated_report}`}
                target="_blank"
                rel="noopener noreferrer"
                onClick={onPdfOpen}
              >
                <img
                  src={`${assestsUserFinanceUrl}/receipt.svg`}
                  alt="receipt icon"
                  className={style.iowc_pdf_icon}
                />
              </a>
            ) : null}
            <h6>
              <span
                style={{
                  fontWeight: "500",
                }}
              >
                {t("type")}
                {": "}
              </span>

              {outward?.outward_type ?? ""}
            </h6>
            <h6>
              <span
                style={{
                  fontWeight: "500",
                }}
              >
                {t("created_at")}
                {": "}
              </span>
              {dayjs(outward?.created_at)?.format("DD/MM/YYYY")},
              {localTimeFormat(outward?.created_at, "LT")}
            </h6>
          </div>
        </div>
      </Link>
    </>
  );
};

export default InOutwardReceiveRequestApprovedCard;
